import { useState, useEffect } from "react";
import {
  getInfoSiteList,
  siteAdd,
  siteDelete,
  siteNewOrderSave,
  siteUpdate, //  API 
} from "../service/helpInfoApi";
import { useGlobalState } from "../context/GlobalStateContext";

export function useHelpInfo() {
  const [siteList, setSiteList] = useState([]);
  const { updateGlobalState } = useGlobalState(); //    

  const fetchSiteList = async () => {
    updateGlobalState("isLoading", true);
    try {
      const data = await getInfoSiteList();
      setSiteList(data);
    } catch (error) {
      console.error("  :", error);
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const addSite = async (img, link, title, description) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteAdd(img, link, title, description);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const deleteSite = async (siteId) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteDelete(siteId);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const saveNewOrder = async () => {
    updateGlobalState("isLoading", true);
    try {
      const orderPayload = siteList.map((site) => ({
        siteInfoId: site.siteInfoId,
        indexOrder: site.indexOrder,
      }));

      const data = await siteNewOrderSave(orderPayload);
      if (data.code === 200) {
        console.log("  :", siteList);
      } else {
        console.error("  :", data.message);
      }
      return data;
    } catch (error) {
      console.error("  API  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  const updateSite = async (siteInfoId, img, link, title, description) => {
    updateGlobalState("isLoading", true);
    try {
      const data = await siteUpdate(siteInfoId, img, link, title, description);
      if (data.code === 200) {
        fetchSiteList();
      }
      return data;
    } catch (error) {
      console.error("  :", error);
      throw error;
    } finally {
      updateGlobalState("isLoading", false);
    }
  };

  useEffect(() => {
    fetchSiteList();
  }, []);

  return {
    siteList,
    setSiteList,
    addSite,
    deleteSite,
    saveNewOrder,
    updateSite, //   
  };
}
